import {api} from '../../../application/Api';

export default class Teams {
    static getByOwner = async (ownerId: string) =>
        api.get(`team/owner/${ownerId}`)

    static deactivate = async (data: any) =>
        api.patch('team/deactivate', data);
}

export interface TeamCreateModel {
    teamName: string;
}

export interface TeamEditModel {
    teamName: string;
    selectedTeam: TeamListItemModel;
}

export interface TeamListModel {
    items: TeamListItemModel[];
}

export interface TeamListItemModel {
    uuid: string,
    name: string,
    createdOn: Date,
    isActive: boolean
}