import {ViewModel} from '../../../application/ViewModel';
import Teams, {TeamListModel} from '../models/TeamModels';


export default class TeamListViewModel extends ViewModel<TeamListModel> {
    constructor(props: any) {
        super(props, {initial: {items: [] } as TeamListModel});
    }
    
    componentDidMount() {
        //this.load();
    }

    load(): void {
        Teams.getByOwner('3fa85f64-5717-4562-b3fc-2c963f66afa6')
                .then((response) => {
                    this.setState({payload: response.data.data})
                })
    }

    deactivate(teamUuid: string){
        let data = {
            'ownerUuid': '3fa85f64-5717-4562-b3fc-2c963f66afa6',
            'teamUuid': teamUuid
        };
        
        Teams.deactivate(data)
            .then(() => {
                this.load();
            })
    }
}